//axios
// import Vue from 'vue'
// import VueRouter from 'vue-router'
// Vue.use(VueRouter);
// import router from 'vue-router';
import router from '../router';
import axios from "axios";
import store from "@/store";
import qs from 'qs';
import { Toast } from 'vant';

//store
//
//axios
let baseURL = "/api";

let requests = axios.create({
  //
  baseURL: process.env.NODE_ENV === 'development' ? baseURL : 'http://116.204.116.99:8002/',
  //5S
  timeout: 5000,
  headers: {"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8" }
});

//----
requests.interceptors.request.use((config) => {
  store.commit('getLoading', true)
  // console.log(process.env.NODE_ENV,config.data)
  config.data = qs.stringify(config.data);
  return config;
});

//----
requests.interceptors.response.use(
  (res) => {
    // console.log(res)
    store.commit('getLoading', false)
    let {data = {}}=res;
    // 
    if(!data.responseCode || data.responseCode==="9999"){
      router.push({path: '/login'})
      return;
    } else {
      return data;
    }
  },
  (err) => {
    console.log('err====',err)
    Toast.fail('');
    store.commit('getLoading', false)
    // alert("");
  }
);
//
//axios
export default requests;
