const state ={
  novelInfo: {}, // 
  novelTypeList: [], // 
  novelPeopleList: [], // 
  novelSectList: [], // 
  novelKungfuList: [], // 
  novelWeaponList: [], // 
  novelFatalismList: [], // 
  novelOtherList: [],  // 
}

const mutations ={
  getNovelInfo(state, data) { // 
    state.novelInfo = data;
  },
  getNovelTypeList(state, data) { // 
    state.novelTypeList = data;
  },
  getNovelPeopleList(state, data) { // 
    state.novelPeopleList = data;
  },
  getNovelSectList(state, data) { // 
    state.novelSectList = data;
  },
  getNovelKungfuList(state, data) { // 
    state.novelKungfuList = data;
  },
  getNovelWeaponList(state, data) {  // 
    state.novelWeaponList = data;
  },
  getNovelFatalismList(state, data) {  //  
    state.novelFatalismList = data;
  },
  getNovelOtherList(state, data) {  //  
    state.novelOtherList = data;
  }
}

const actions ={
  getNovelInfo({commit}, data) { // 
    commit('getNovelInfo', data);
  },
  getNovelTypeList({commit}, data) { // 
    commit('getNovelTypeList', data);
  },
  getNovelPeopleList({commit}, data) { // 
    commit('getNovelPeopleList', data);
  },
  getNovelSectList({commit}, data) { // 
    commit('getNovelSectList', data);
  },
  getNovelKungfuList({commit}, data) { // 
    commit('getNovelKungfuList', data);
  },
  getNovelWeaponList({commit}, data) {  // 
    commit('getNovelWeaponList', data);
  },
  getNovelFatalismList({commit}, data) {  // 
    commit('getNovelFatalismList', data);
  },
  getNovelOtherList({commit}, data) {  // 
    commit('getNovelOtherList', data);
  }
}

const getters ={}

export default {
  state,
  mutations,
  actions,
  getters
};